

// ==== BUTTONS:
.btn {
  display: inline-block;
  border: none;
  padding: .8rem;
  border-radius: .3rem;
  cursor: pointer;
  font-size: 1.4rem;
  font-weight: 500;
  background-color: transparent;
  color: #fff; 
  transition: all 400ms ease;

  // &:hover {
  //   background-color: #fff;
  //   color: #555;
  // }
}

.btn.hover.btn:hover { 
  background-color: #fff;
  color: #555;
};



.hidden {
  display: none;
}

.active {
  display: block;
}

.todo-active {
  order: 1;
}



